@import "./variables/variables.scss";

.newPlanModal {
  align-self: center;
  height: 500px;
  width: 50%;
  background-color: $greyColor;
  color: $lightColor;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  border-radius: $borderRadius;
}
